.checkbox__input {
  display: none;
}

.checkbox__input:checked + label:after {
  opacity: 1;
}

.checkbox__label {
  position: relative;
  padding-left: 40px;
  cursor: pointer;
  min-height: 24px;
  display: flex;
  align-items: center;
}

.checkbox__label:before, .checkbox__label:after {
  position: absolute;
  content: "";
}

.checkbox__label:before {
  background: #fff;
  height: 23px;
  width: 23px;
  left: 0;
  top: 0px;
}

.checkbox__label:after {
  transition: opacity .3s ease;
  height: 6px;
  width: 11px;
  left: 6px;
  top: 9px;
  background: url("../img/ico-checbox-check.png") no-repeat;
  opacity: 0;
}

.radio__input {
  display: none;
}

.radio__input:checked + label:after {
  opacity: 1;
}

.radio__label {
  padding-left: 30px;
  min-height: 15px;
  position: relative;
  display: block;
  cursor: pointer;
}

.radio__label:before, .radio__label:after {
  content: "";
  position: absolute;
  display: block;
  border-radius: 50%;
}

.radio__label:before {
  border: 1px solid #e4e8ea;
  height: 15px;
  width: 15px;
  left: 0;
  top: 0;
}

.radio__label:after {
  top: 4px;
  left: 4px;
  background: #1e57fa;
  height: 7px;
  width: 7px;
  transition: opacity .3s ease;
  opacity: 0;
}

