    margin-bottom: 25px;
  }
  .testemonials .bot {
    flex-direction: column;
  }
  .testemonials-text {
    max-width: 100%;
    margin-top: 20px;
  }
  .testemonials-arrow {
    transform: translate3d(-14px, 0, 0);
  }
  .map {
    margin-top: 50px;
  }
  .map-info {
    margin: 0;
    border: 0;
  }
  .map-info-cont {
    position: static;
    margin: 20px 0;
  }
  .map-info__title {
    font-size: 1.2rem;
    padding: 20px 40px;
  }
  .map-info .right {
    padding: 0 0 0 30px;
  }
  .links {
    display: none;
  }
  .soc {
    transform: translate3d(-85px, 0, 0);
  }
  .head {
    padding: 1.4rem;
  }
  .head-phone {
    display: none;
  }
  #content {
    transition: transform .3s ease;
  }
  .burger {
    z-index: 9999;
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: content-box;
    width: 30px;
    height: 30px;
    padding: 15px;
    background: 0 0;
    border: 0;
    user-select: none;
    margin: -15px;
    margin-left: auto;
    cursor: pointer;
  }
  .burger:before {
    display: none;
  }
  .burger span {
    position: relative;
    display: inline-block;
    width: 30px;
    height: 2px;
    border-radius: 5px;
    vertical-align: bottom;
    background: #000;
    transition: background .2s .4s;
  }
  .burger span:before {
    border-radius: 5px;
    transform: none;
    content: "";
    position: absolute;
    left: 0;
    top: 10px;
    width: 100%;
    height: 2px;
    background: #000;
    transform-origin: 50% 50%;
    transition: top .3s .3s,transform .3s;
  }
  .burger span:after {
    border-radius: 5px;
    content: "";
    position: absolute;
    left: 0;
    top: -10px;
    width: 100%;
    height: 2px;
    background: #000;
    transform-origin: 50% 50%;
    transition: top .3s .3s,transform .3s;
  }
  .main-nav--open {
    overflow: hidden;
  }
  .main-nav--open footer {
    transform: translate3d(-250px, 0, 0);
  }
  .main-nav--open .mobile-menu {
    overflow-x: hidden;
  }
  .main-nav--open .burger > span {
    background: 0 0;
  }
  .main-nav--open .burger > span::before {
    transform: rotate3d(0, 0, 1, 45deg);
    top: 0;
    transition: top .3s,transform .3s .5s;
  }
  .main-nav--open .burger > span::after {
    transform: rotate3d(0, 0, 1, -45deg);
    top: 0;
    transition: top .3s,transform .3s .5s;
  }
  .main-nav--open .menu {
    transform: translate3d(0, 0, 0);
  }
  .main-nav--open .menu__item {
    transform: translate3d(0, 0, 0);
  }
  .main-nav--open #content {
    transform: translate3d(-250px, 0, 0);
  }
  .mobile-menu {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
    width: 250px;
    border-left: 1px solid #eee;
  }
  .slider-nav__list {
    flex-direction: column;
  }
  .slider-nav__link {
    padding: 15px;
    background: #fff !important;
    color: #000 !important;
    border-bottom: 1px solid #eee;
  }
}

