		max-width: 820px; } }

@media screen and (max-width: 920px) {

	.about {
		padding-top: 40px;

		&__text {
			&:before {
				display: none; } }

		&-cont {
			flex-direction: column; }

		&-gallery {
			max-width: 100%;
			margin: 30px 0 0 0;

			&__list {
				flex-direction: row; }

			&__item {
				&:not(:first-child) {
					margin: 0 0 0 1.4rem; } } } }

	.advantages {
		margin-top: 45px;

		&-slider {
			&__item {
				padding: 30px;
				margin: 0 20px; }

			.slick-list {
				margin: 0 -20px; }

			&__img {
				margin-bottom: 25px; } } }

	.projects__title {
		max-width: 240px;
		padding: 25px; }

	.main-slider__nav {
		display: none; }

	.testemonials {
		&__slider {
			padding: 35px; }

		.top {
			margin-bottom: 25px; }

		.bot {
			flex-direction: column; }

		&-text {
			max-width: 100%;
			margin-top: 20px; }

		&-arrow {
			transform: translate3d(-14px, 0, 0); } }

	.map {
		margin-top: 50px;

		&-info {
			margin: 0;
			border: 0;
			&-cont {
				position: static;
				margin: 20px 0; }
			&__title {
				font-size: 1.2rem;
				padding: 20px 40px; }

			.right {
				padding: 0 0 0 30px; } } }

	.links {
		display: none; }

	.soc {
		transform: translate3d(-85px, 0, 0); }

	.head {
		padding: 1.4rem; }

	.head-phone {
		display: none; }
	#content {
		transition: transform .3s ease; }

	.burger {
		z-index: 9999;
		display: flex;
		justify-content: center;
		align-items: center;
		box-sizing: content-box;
		width: 30px;
		height: 30px;
		padding: 15px;
		background: 0 0;
		border: 0;
		user-select: none;
		margin: -15px;
		margin-left: auto;
		cursor: pointer;

		&:before {
			display: none; }
		span {
			position: relative;
			display: inline-block;
			width: 30px;
			height: 2px;
			border-radius: 5px;
			vertical-align: bottom;
			background: #000;
			transition: background .2s .4s;
			&:before {
				border-radius: 5px;
				transform: none;
				content: "";
				position: absolute;
				left: 0;
				top: 10px;
				width: 100%;
				height: 2px;
				background: #000;
				transform-origin: 50% 50%;
				transition: top .3s .3s,transform .3s; }
			&:after {
				border-radius: 5px;
				content: "";
				position: absolute;
				left: 0;
				top: -10px;
				width: 100%;
				height: 2px;
				background: #000;
				transform-origin: 50% 50%;
				transition: top .3s .3s,transform .3s; } } }

	.main-nav--open {
		overflow: hidden;

		footer {
			transform: translate3d(-250px, 0, 0); }
		.mobile-menu {
			overflow-x: hidden; }

		.burger>span {
			background: 0 0; }
		.burger>span::before {
			transform: rotate3d(0,0,1,45deg);
			top: 0;
			transition: top .3s,transform .3s .5s; }
		.burger>span::after {
			transform: rotate3d(0,0,1,-45deg);
			top: 0;
			transition: top .3s,transform .3s .5s; }
		.menu {
			transform: translate3d(0,0,0);

			&__item {
				transform: translate3d(0,0,0); } }

		#content {
			transform: translate3d(-250px,0,0); } }

	.mobile-menu {
		display: block;
		position: absolute;
		top: 0;
		right: 0;
		height: 100%;
		width: 250px;
		border-left: 1px solid #eee; }

	.slider-nav {
		&__list {
			flex-direction: column; }

		&__link {
			padding: 15px;
			background: #fff !important;
			color: #000 !important;
			border-bottom: 1px solid #eee; } } }



