$output-bourbon-deprecation-warnings: false;
@import "bourbon";
@import "vars";
@import "base";
@import "functions";



.head {
	padding: 35px rem(60) 33px;
	position: sticky;
	top: 0;
	z-index: 99999999999;
	background: #fff;
	border-bottom: 2px solid transparent;
	transition: padding .3s ease;

	&.fixed {
		border-bottom: 2px solid #eceeee;
		padding: 20px 3.75rem 20px; }

	&-cont {
		display: flex;
		justify-content: space-between;
		align-items: center; }

	&-logo {

		img {
			display: block; } }

	&-phone {
		display: flex;
		flex-direction: column;
		color: #000;
		background: url('../img/ico-phone.png') no-repeat left center;
		padding-left: 61px;
		margin: 0 3px 8px 0;

		a {
			color: currentColor;
			text-decoration: none;
			font-family: "Montserrat";
			font-weight: 500; } }

	&-link {
		font-family: "Montserrat";
		font-weight: 400;
		font-size: rem(14);
		margin-top: 8px; } }

.tel {
	font-family: "Montserrat";
	font-size: rem(18);
	font-weight: 500; }

.nav {
	font-family: "Montserrat";

	&__list {
		display: flex;
		justify-content: space-between; }

	&__item {

		&:not(:first-child) {
			margin-left: 61px; } }

	&__link {
		position: relative;
		font-size: rem(14);
		font-weight: bold;
		text-transform: uppercase;
		text-decoration: none;
		padding: 8px 6px;
		color: #000;
		white-space: nowrap;
		font-family: "Montserrat";

		&:after {
			content: "";
			display: block;
			background: #000;
			height: 2px;
			width: 100%;
			position: absolute;
			bottom: 0;
			left: 0;
			opacity: 0;
			transform: translate3d(0, 100%, 0);
			transition: transform .3s ease, opacity .3 ease; }

		&.active, &:hover {
			&:after {
				transform: translate3d(0, 0, 0);
				opacity: 1; } } } }

.main-slider {

	&__nav {
		background: #184bdb; } }

