.main-slider {
	&__cont {
		position: relative;
 }		// margin-bottom: -85px

	&-arrow {
		position: absolute;
		bottom: -85px;
		right: 0;
		z-index: 2;
		transform: translate3d(0,50%,0); }

	&.slick-initialized {

		.main-slider__slide {
			display: block; } }


	&__slide {

		&:not(:first-child) {
			display: none; }

		img {
			width: 100%; } } }

.slick {
	&-arrow {
		position: relative;
		border: 0;
		background: #000;
		font-size: 0;
		@include size(58px);
		padding: 0;
		transition: background .3s ease;

		&:hover, &:active {
			background: #171717; }

		&:before {
			content: "";
			display: block;
			position: absolute;
			top: calc(50% - 7px);
			left: calc(50% - 11px);
			@include size(15px);
			border: {
				top: 1px solid #fff;
				right: 1px solid #fff; }
			transform: rotate(45deg); } }
	&-prev {
		transform: rotate(180deg); } }

.title {
	position: relative;
	font-size: rem(30);
	font-weight: bold;
	line-height: 1.2;

	&:before {
		content: "";
		height: 1px;
		width: 56px;
		position: absolute;
		left: -113px;
		top: 18px;
		display: block;
		background: #000; } }




.about {
	position: relative;
	z-index: 10;
	pointer-events: none;

